.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap:15%
}

.about__me {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

}

figure{
    position: relative;
    overflow: hidden;
    cursor: pointer;
}

figure:hover > .capa{
    opacity: 1;
    visibility: visible;
}


figure:hover > img{
    transform: scale(1.3);
    transition: var(--transition);
    filter: blur(0.5rem);

}

img{
    transform: scale(1);
    transition: var(--transition);

}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    transition: var(--transition);

}


.capa{
    color: var(--color-bg);
    position: absolute;
    top:0;
    width: 100%;
    height: 100%;
    background: rgba(151, 41, 255, 0.6);
    opacity: 0;
    transition: var(--transition);
    visibility: hidden;
    text-align: center;
    padding: 3%;
}

.capa h3{
    margin-top: 70px;
    margin-bottom: 15px;
    transition: var(--transition);

}


.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;

}

.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 1.2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor:default;
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;

}

.about__card > h5{
    font-size: 0.95rem;
}

.about__card > small{
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content > p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}


/* Dispositivos medianos*/

@media screen and (max-width: 1024px){

    .about__container{
        grid-template-columns: 1fr;
        gap:0;
    }

    .about__me{
        width:50%;
        margin: 2rem auto 4rem;
    }
    
    .about__content >p{
        margin: 1rem 0 1.5rem;
    }

  }
  
  /* Dispositivos pequeños*/
  
  
  @media screen and (max-width: 630px){
  

    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }

    .about__content{
        text-align: center;
    }

    .about__content > p{
        margin: 1.5rem 0;
    }
    
  }